import logo from './logo.svg';
import './App.css';
import React, { useState } from 'react';

class App extends React.Component {

  constructor(props) {
    super(props);

    let strIndex = 0;
    let textToRead = "Hello";

    this.state = {
      strIndex: strIndex,
      textToRead: textToRead,
      currentChar: textToRead[strIndex],
      currentX: 0,
      currentY: 0,
      mouseDown: "TRUE", 
    }
  }

  _onMouseMove(e) {
    this.setState({ 
      ...this.state,
      currentX: e.screenX, 
      currentY: e.screenY 
    });
  }

  handleEvent = (event) => {
    if (event.type === "mousedown") {
      // this.setState({ message: "Mouse Down"});
      // this.setState({message: "Mouse Down"});
      // console.log("DOWN!")
      this.setState({
        ...this.state,
        mouseDown: "TRUE",
      })
    } else {
        // this.setState({ message: "Mouse Up"});
      // this.setState({message: "Mouse Up"})
      // console.log("UP!")
      this.setState({
        ...this.state,
        mouseDown: "FALSE",
      })
    }
  }

  handleDouble = (event) => {
    let newStrIndex = this.state.strIndex + 1;
    if (event.type === "dblclick") {
      this.setState({
        ...this.state,
        strIndex: newStrIndex,
        currentChar: this.state.textToRead[newStrIndex]
      })

    }
  }

  // onClickHandler = event => {
  //   clearTimeout(timer);
  //   if (event.detail === 1) {
  //       timer = setTimeout(this.props.onClick, 200)
  //       console.log("1")
  //   } else if (event.detail === 2) {
  //       this.props.onDoubleClick()
  //       console.log("2")
  //   }
  // }

  render() {
    return (
      <div className="App" 
        onMouseDown={this.handleEvent} 
        onMouseUp={this.handleEvent}
        onDoubleClick={this.handleDouble}
        onMouseMove={this._onMouseMove.bind(this)}>
        <div>
          <button>Hello1</button>
          <button>Hello4</button>
        </div>
        <div>
          <button>Hello2</button>
          <button>Hello5</button>
        </div>
        <div>
          <button>Hello3</button>
          <button>Hello6</button>
        </div>
        <div>{this.state.strIndex}</div>
        <div>{this.state.currentChar}</div>
        <div>x: {this.state.currentX} y: {this.state.currentY}</div>
        <div>{this.state.mouseDown}</div>
      </div>
    );
  }
}

export default App;
